main {
    border-style: hidden;
}

h2 {
    color: black;
}

h3 {
    color: black;
}


a {
    color: black;
}

p {
    line-height: 1em;
    color: rgba(0, 0, 0, 0.473);
    text-align: end;
    font-size: 42px;
    font-family: Amatic SC;
}

img:hover {
  box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
}

#grayscale {
  
  border-radius: 40%;
  box-shadow: 10px 10px 5px lightgrey;
  /* Add the blur effect */
  filter: blur(8px);
  -webkit-filter: blur(8px);
}
#grayscale:hover {
filter: blur(0);
  -webkit-filter: blur();
}
footer {
    border-style: hidden;
    position: inherit;
    left: 0;
    bottom: 0;
    width: 100%;

  background: linear-gradient(45deg, black, lightgray, blue, orange);
    background-size: 400% 400%;
  -webkit-animation: gradient 10s ease infinite;
          animation: gradient 10s ease infinite;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}





